<template>
  <div>
    <c-title :text="title" :hide="false"></c-title>
    <p>{{ message }}</p>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import cTitle from 'components/title';

  export default {
    data () {
      return {
        title: 'TEST PAGE!',
      }
    },
    computed: mapState([
      'message'
    ]),
    mounted: function () {
      this.$store.commit('MESSAGE', 'into test page！');
    },
    components: {cTitle}
  }
</script>


